import React, { useEffect } from 'react';
import { GG_CLIENT_ID } from '../../../actions/constants';
import { toast } from 'react-toastify';

const GoogleLoginSuggestions = ({loginAction}) => {
    const url = 'https://accounts.google.com/gsi/client';

    const parseJwt = (token) => {
        const base64Url = token.split('.')[1];
        const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
        const jsonPayload = decodeURIComponent(window.atob(base64).split('').map(function(c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
        }).join(''));
        return JSON.parse(jsonPayload);
    }

    const googlePromptFun = () => {
        if (window.google && Object.keys(window.google).length > 0 && window.google.accounts) {
            window.google.accounts.id.initialize({
                client_id: GG_CLIENT_ID,
                callback: (response) => {
                    if (response.credential) {
                        try {
                            const data = parseJwt(response.credential);
                            loginAction(data);
                        } catch (error) {
                            toast.error('Something went wrong.', { autoClose: 3000 })
                        }
                    }
                },
            });
            window.google.accounts.id.prompt();
        }
    }

    useEffect(() => {
        const script = document.createElement('script');
        script.src = url;
        script.async = true;
        script.defer = true;
        document.body.appendChild(script);
        googlePromptFun()
        return () => {
            document.body.removeChild(script);
        };
    }, []);

    return <></>
};

export default GoogleLoginSuggestions;
