import React from "react";
import { Modal, Image } from "react-bootstrap";
import { ENV, IS_TAGEMBED, WASABI_PATH } from "../../actions/api";

const TagembedRedirect = ({ show, onHide }) => {
    return <Modal size="sm" show={show} className="px-3" onHide={onHide} backdrop="static" aria-labelledby="contained-modal-title-vcenter" keyboard={false} centered>
        <Modal.Body className="p-0">
            <div className="popup-wrap">
                <div className="popup-inn">
                    <div className="popup-head">
                        <Image className="py-2" src={`${WASABI_PATH}app/assets/popup/box-embed.png`} alt="tag" />
                    </div>
                    <p>{IS_TAGEMBED ? `You are an existing member at Tagbox. As Tagembed is part of Tagbox group of companies, enter the same credentials used for Tagbox to log in.` : `You are an existing member at Tagembed (Tagbox's partner platform). To log in, simply log in with Tagembed credentials or try another email address.`}</p>
                    <div className="d-flex align-items-center justify-content-between p-5">
                        <span className="popup-try" onClick={onHide}>Try Another Email</span>
                        <button className="btn btn-primary" onClick={() => window.open(`https://${ENV}.${IS_TAGEMBED ? 'taggbox' : 'tagembed'}.com/accounts/login`, "_self")}>Go To Login Page</button>
                    </div>
                </div>
            </div>
        </Modal.Body>
    </Modal>
}

export default TagembedRedirect;