import React from 'react'
import { Button, Image } from 'react-bootstrap'
import { baseUrl } from '../../../Assets/AssetsHelper'
import { getGoogleCode } from '../../../utils/auth';

const SocialSignIn = ({ text }) => {
    const getGoogleAuth = () => {
        localStorage.setItem("connectionType", 'sign-login');
        getGoogleCode();
    }
    return <div className="login_socials text-center">
        <Button variant='light' size='lg' id="googleplusSignIn" onClick={getGoogleAuth} style={{ minWidth: 120 }} className='w-100 border-0 fw-bold '>
            <Image className="me-2 h-20px w-20px" src={baseUrl('media/icons/social-icons/google.svg')} width="20" height="20" alt='Google'/>
            {text || `Log in with Google`}
        </Button>
        <div className="d-flex align-items-center flex-column justify-content-between py-4 w-100">
            <div className="separator separator-content my-2 my-md-3 w-100">
                <span className="text-muted fw-semibold fs-6 f-center text-nowrap" style={{ width: 50 }}>Or</span>
            </div>
        </div>
    </div>
}
export default SocialSignIn