import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { userSignUp, loginUser } from '../../actions/loginActions'
import SocialSignIn from "./SocialAuthentication/SocialSignup";
// import OtherAuthAction from "./Components/OtherAuthAction";
import { getCookieValue, parseAUTHJson, TOAST_ERROR, validateEmail } from "../../utils";
import { FontAwesomeIcon } from "../../Assets/AssetsHelper";
import { Cookies } from "react-cookie";
import { Form, Button, Alert } from 'react-bootstrap';
import { GOOGLE_AUTH_TOKEN } from "../../actions/socialUrl";
import { removeAuthLocalData } from "../../utils/addAccount";
import queryString from 'query-string';
import GoogleLoginSuggestions from "./Components/GoogleLoginSuggestions";
import { googleUserDetails } from "../../utils/auth";
import FormTerms from "./Components/FormTerms";
import OtherAuthAction from "./Components/OtherAuthAction";
import { DOMAIN_NAME } from "../../actions/api";
import TagembedRedirect from "./TagembedRedirection";
import { sendCode } from "../../utils/Authorization";

const cookies = new Cookies();
class Login extends Component {
    constructor(props) { super(props); }
    state = { accountEmail: null, accountPassword: null, emailError: null, passwordError: null, loginLoader: false, loginMailError: null, memberRemember: true, showPassword: false, resetAlert: 0, loginPwdError: null, tagembedRedirect: false }

    handleMessage = async (event) => {
        try {
            const response = parseAUTHJson(event.data);
            const { code, type } = response;
            if (type == 'sign-login') {
                if (code) {
                    const { data: { responseData } } = await GOOGLE_AUTH_TOKEN(code);
                    const { userInfo } = responseData || {}
                    removeAuthLocalData();
                    this.socialLogin({ accountFullName: userInfo?.name, accountEmailId: userInfo?.email, createFrom: 'google', picture: userInfo?.picture || "", authId: userInfo?.sub });
                };
            }
        } catch (error) {
        }
    };

    componentDidMount() {
        window.addEventListener("message", this.handleMessage)
        // Set data-product attribute on <html> tag for login screen
        try {
            document.documentElement.setAttribute("data-product", DOMAIN_NAME);
        } catch (e) {}
        const parsed = queryString.parse(window.location.search);
        const { email, password, status } = cookies.get('__RememberDetails') || {}
        this.setState({ accountEmail: email || '', accountPassword: password || '', memberRemember: status || false })
        if (parsed && Object.keys(parsed.length > 0)) {
            this.setState({ resetAlert: parsed.reset })
        }
    }

    readAuthentication = (event) => {
        const response = JSON.parse(event.data);
        this.socialLogin(response)
    }

    emailTextChange = event => this.setState({ accountEmail: event.target.value, emailError: null, loginMailError: null })
    passwordTextChange = event => this.setState({ accountPassword: event.target.value, passwordError: null, loginPwdError: null })

    clickOnLogin = event => {
        const { accountEmail, accountPassword, memberRemember } = this.state

        this.setState({ loginLoader: true })
        localStorage.setItem("socialLogin", 0)
        if (String(accountEmail)?.trim().length > 0 && validateEmail(accountEmail) && String(accountPassword)?.trim().length > 0) {
            const userData = { emailId: accountEmail, password: accountPassword, loginType: "web" }
            const RememberDetails = { email: accountEmail, password: accountPassword, status: true }
            this.props.loginUser(userData, memberRemember, RememberDetails, this.props.history, this.callBackError);
        }
        else {
            if (!accountEmail || String(accountEmail).trim().length == 0 || !validateEmail(accountEmail)) this.setState({ emailError: true })
            if (!accountPassword || String(accountPassword).trim().length == 0) this.setState({ passwordError: true })
            this.setState({ loginLoader: false })
        }
    }

    responseCallback = (message) => {
        const isValidMsg = (message && String(message).length > 0);
        this.setState({ tagembedRedirect: isValidMsg ? message == "showRedirectionPop" : false, loginLoader: false });
    }

    getGoogleUserDetails = (token) => {
        googleUserDetails(token).then(res => this.onGSocialLogin("google", res.data));
    }

    onGSocialLogin = (provider, data) => {
        const trackingPage = getCookieValue('tracking_URL')
        const landingPage = getCookieValue('landing_URL')
        if (data && Object.keys(data).length > 0) {
            var picture = data.picture ? data.picture : "";
            this.setState({ loginLoader: true });
            this.props.userSignUp({ fullName: data.name, emailId: data.email, password: "", trackingPage: trackingPage, landingPage: landingPage, activeProduct: "0", createFrom: provider, picture: picture, oauth_uid: data.sub, utm_source: provider }, this.props.history, this.responseCallback)
        }
    }

    socialLogin = (socialData) => {
        var { accountFullName, accountEmailId, createFrom, picture, authId } = socialData
        localStorage.setItem("socialLogin", 1)
        const trackingPage = getCookieValue('tracking_URL')
        const landingPage = getCookieValue('landing_URL')
        this.props.userSignUp({ fullName: accountFullName, emailId: accountEmailId, password: "", trackingPage: trackingPage, landingPage: landingPage, activeProduct: "0", createFrom: createFrom, picture: picture, oauth_uid: authId, utm_source: createFrom == 'facebook' ? "facebook_ads" : createFrom }, this.props.history, this.responseCallback)
    }

    callBackError = (message) => this.setState({ loginMailError: String(message).toLowerCase().includes('email') ? message : '', loginLoader: false, emailError: String(message).toLowerCase().includes('email') ? true : false, passwordError: String(message).toLowerCase().includes('password') ? true : false, loginPwdError: String(message).toLowerCase().includes('password') ? message : '' }, () => { if (!(String(message).includes('email') || String(message).toLowerCase().includes('email') || String(message).toLowerCase().includes('password'))) TOAST_ERROR(message) })

    componentWillUnmount() {
        window.removeEventListener("message", this.handleMessage);
    }
    
    render() {
        const { accountEmail, accountPassword, emailError, passwordError, loginLoader, loginPwdError, memberRemember, showPassword, resetAlert, loginMailError, tagembedRedirect } = this.state;
        return <><div className="w-100 mb-lg-0 mb-3">
            <GoogleLoginSuggestions loginAction={(data) => this.onGSocialLogin('google', data)} key={window.location.href} />
            {resetAlert ? <Alert variant={resetAlert == 1 ? 'success' : 'danger'} className="fs-7 rounded-2" dismissible>
                {resetAlert == 1 ? 'You have successfully updated your password. You can login now with your new password.' : 'Link expired, please try again.'}
            </Alert> : null}
            <div className="text-start">
                <h1 className=" mb-0 display-2 fw-bold mb-5">
                    Welcome back
                </h1>
                <SocialSignIn socialLogin={this.socialLogin} text="Log in with Google" />
            </div>
            <form autoComplete="on" onKeyPress={e => e.key == 'Enter' ? this.clickOnLogin() : ''}>
                <div className="mb-8">
                    <Form.Group className="mb-4 mb-xl-5">
                        <Form.Label className="fw-bold " htmlFor="email_">Email</Form.Label>
                        <Form.Control type="email" size="lg" placeholder="you@yourcompany.com" className={`${emailError ? `is-invalid` : ``} mb-1`} value={accountEmail || ''} onChange={this.emailTextChange} title="Please enter email." />
                        {emailError ? <span className="invalid-feedback">{loginMailError ? loginMailError : accountEmail?.length ? 'Enter valid email.' : 'Email is required.'}</span> : null}
                    </Form.Group>
                    <Form.Group className="mb-4 mb-xl-5">
                        <Form.Label className="fw-bold " htmlFor="password_">Password</Form.Label>
                        <span className={`position-relative d-block ${passwordError ? `is-invalid` : ``}`}>
                            <Form.Control type={`${showPassword ? 'text' : 'password'}`} placeholder="Password" size="lg" className={`${passwordError ? `is-invalid` : ``} mb-1`} onChange={this.passwordTextChange} value={accountPassword || ''} title="Please enter password." />
                            <span className="view_pass fs-7 btn btn-icon bg-transparent border-0 text-gray-500 position-absolute translate-middle top-50" onClick={event => this.setState({ showPassword: !showPassword })}>
                                <FontAwesomeIcon icon={showPassword ? 'eye' : 'eye-slash'} />
                            </span>
                        </span>
                        {passwordError ? <div className="invalid-feedback">{loginPwdError ? loginPwdError : 'Password is required.'}</div> : null}
                    </Form.Group>
                    <div className="d-flex justify-content-between align-items-center fw-semibold mb-4 mb-xl-5 pt-1">
                        <label className="form-check align-items-center mb-0">
                            <input className="form-check-input" name="remember" type="checkbox" checked={memberRemember} onChange={e => this.setState({ memberRemember: e.target.checked })} />
                            <span className="form-check-label fw-semibold">Remember Me</span>
                        </label>
                        <Link className="pull-right forgot_password" to={`/accounts/forgotpassword`} title="Forgot Password">Forgot Password?</Link>
                    </div>
                </div>
                <div className="d-grid mb-3" >
                    <Button variant="primary" size={'lg'} className={`${loginLoader ? ' disabled' : ''}`} onClick={loginLoader ? null : this.clickOnLogin} >
                        <span className="indicator-label" style={{ display: loginLoader ? 'none' : 'block' }}>Log in with Email</span>
                        <span className="indicator-progress" style={{ display: !loginLoader ? 'none' : 'block' }}> Please wait...
                            <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                        </span>
                    </Button>
                </div>
            </form>
            <OtherAuthAction text="Not a member yet?" link={'/accounts/register'} linkText={'Sign up'} />
        </div>
            <FormTerms />
            <TagembedRedirect show={tagembedRedirect} onHide={() => this.setState({ tagembedRedirect: false })} />
        </>
    }
}
const mapStateToProps = (state) => {
    return {}
}
const mapDispatchToProps = dispatch => {
    return {
        userSignUp: (data, history, responseCallback) => dispatch(userSignUp(data, history, responseCallback)),
        loginUser: (postbody, memberRemember, RememberDetails, history, callback) => dispatch(loginUser(postbody, memberRemember, RememberDetails, history, callback))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Login);